import { isIOS, isAndroid } from '@/utils/platform'
import { recentVersionApi } from '@/api/about'
import { H5OpenApp, isWeixinBrowser } from '@/utils/openApp'
import pageHeader from '@/components/pageHeader'
import netBroken from '@/components/ErrorPage/netBroken'
export default {
  name: 'abouts',
  components: {
    pageHeader,
    netBroken
  },
  data () {
    return {
      versonInfo: {},
      isShow: false,
      isNetError: false
    }
  },
  created () {
    // window.navigator.onLine ? this.requestData() : this.isNetError = !window.navigator.onLine
    if (window.navigator.onLine) {
      this.requestData()
      return
    }
    if (!window.navigator.onLine) {
      this.isNetError = !window.navigator.onLine
    }
  },
  methods: {
    jumpPage (val, param) {
      if (val == 'useRule' && isIOS()) {
        this.$router.push({
          path: '/useRuleIos',
          query: {
            from: param
          }
        })
        return
      }
      this.$router.push({
        path: '/' + val,
        query: {
          from: param
        }
      })
    },
    openIt () {
      if (isWeixinBrowser()) {
        this.$emit('showMask', true)
      } else {
        let adrPath = ''
        let iosPath = ''
        if (this.openPage == 'detail') {
          var _href = window.location.href
          var reg = /\?from=[^&]*&isappinstalled=[0|1]/
          var href = _href.replace(reg, '')
          href = href.replace('sharedetail', 'detail')
          adrPath = '?jt=1&contId=' + this.contId + '&detailUrl=' + href.split('#')[0].toString() // 安卓没识别出#/detail，传值到.html, #/detail由安卓自己拼接
          if (this.isVideo) {
            iosPath = '?jt=5&contId=' + this.contId// ios可正常识别#/detail, 5是有视频的
          } else {
            iosPath = '?jt=1&contId=' + this.contId + '&detailUrl=' + href.split('#')[0].toString() // ios没识别出#/detail，传值到.html, #/detail由ios自己拼接
          }
        } else if (this.openPage == 'personalPage') {
          adrPath = '?jt=2&uid=' + this.uid
          iosPath = '?jt=6&uid=' + this.uid
        } else if (this.openPage == 'lottery') {
          adrPath = '?jt=4&luckyId=' + this.luckyId
          iosPath = '?jt=4&luckyId=' + this.luckyId
        } else if (this.openPage == 'event') {
          adrPath = '?jt=3&eventId=' + this.eventId
          iosPath = '?jt=3&eventId=' + this.eventId
        }
        // '?jt=1&contId=1085465558880620544&detailUrl=http://dev.cogo.club/api/gmh5/index.html#/detail'
        H5OpenApp({iosPath: iosPath, adrPath: adrPath})
      }
    },
    chooseJumpLink () {
      if (isIOS()) {
        console.log('跳转到ios app store，链接待确认')
        window.location.href = 'http://itunes.apple.com/us/app/id1441507544'
        return
      }
      console.log('跳转到应用宝，链接待确认')
      window.location.href = 'https://a.app.qq.com/o/simple.jsp?pkgname=club.cogo.cogoproject'
    },
    requestData () {
      let appType = isIOS() ? 1 : 0
      let appVersion = this.curVersion.replace(/[v|V]/, '')
      recentVersionApi({
        appType,
        appVersion
      }).then(res => {
        this.isShow = true
        if (res.data.code === 2000) {
          Object.assign(this.versonInfo, res.data.data)
          this.versonInfo.newVersion = 'V' + res.data.data.newVersion.replace(/[v|V]/, '')
        }
      })
    },
    updateApp () {
      if (this.versonInfo.isNewVersion == 0) {
        if (isAndroid() || isIOS()) {
          isIOS() ? this.$device.JsBridge.callHandler('h5ClickUpdate', {}, (data) => {}) : window.glamor.clickUpdate()
          return
        }

        this.$vux.toast('请在ios, andorid设备上使用')
      }
    },
    goBack () {
      if (isAndroid()) {
        console.log('安卓')
        window.glamor.closePage()
        return
      }
      if (isIOS()) {
        console.log('ios')
        this.$device.JsBridge.callHandler('h5LeftBack', {}, (data) => {})
        return
      }
      console.log('web')
      this.$router.go(-1)
    }
  },
  computed: {
    curVersion () {
      return 'V' + this.$route.query.version.replace(/[v|V]/, '')
    }
  }
}



